@import url(https://fonts.googleapis.com/css2?family=Rakkas&display=swap);
@import url(https://use.typekit.net/yvc3ugi.css);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-component {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    background-color: black;
    font-family: "Rakkas", cursive;
    z-index: 1;
}

.header__left {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1 1;
}

.header__left > h1 {
    margin-bottom: 0px;
    color: black;
    letter-spacing: 5px;
    font-size: 40px;
    color: white;
}

.header__right {
    flex: 2 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
}

.header__right > p {
    margin-right: 30px;
    margin-bottom: 0px;
    font-weight: normal;
    font-size: 14px;
    font-style: italic;
    color: white;
}

.ant-menu-inline,
.ant-menu-vertical,
.ant-menu-vertical-left {
    border-right: none !important;
}

.ant-spin {
    width: 100%;
}

.sidebar-component {
    width: 100%;
    height: 100%;
}

.sidebar__top {
    width: 100%;
    left: 0;
    height: 10%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.sidebar__bottom {
    height: 90%;
    overflow-y: scroll;
    scrollbar-width: thin;
}

/* width */
::-webkit-scrollbar {
    width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
    background-color: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}

.country-component, .country__detail__container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.country-component{
    overflow: scroll;
}

.country__detail__container {
    flex-direction: column;
}

.MuiGrid-spacing-xs-3 {
    width: 0 !important;
    margin: 0 !important;
}

.MuiGrid-container {
    width: 100% !important;
    height: 100%;
    align-items: center;
}

.country__detail__paper {
    padding: 10px;
    text-align: center;
    color: black;
}

.country__detail__paper > img {
    width: 100%;
}

.table-name-component, table {
    width: 100%;
}

td, th {
    border: 1px solid #E7E7E7;
}

.row__title {
    font-weight: bold;
}

.homepage-component {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.homepage__time, .homepage__date {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.border__style {
    width: 150px;
    height: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px;
}

.border__style > p, .space__style > p {
    font-family: tekton-pro, sans-serif;
    font-size: 100px;
    margin-bottom: 0px;
}

.border__style__year {
    width: 300px;
}

.space__style {

}


* {
    font-family: "Poppins", sans-serif;
}

.App {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    background-image: url(/thecountry/static/media/bg.e0bea80b.jpg);
    background-size: cover;
}

.header {
    width: 100%;
    height: 8vh;
}

.container {
    width: 100%;
    height: 92vh;
    display: flex;
    flex-direction: row;
}

.sidebar {
    width: 15%;
    height: 100%;
    border-right: 1px solid white;
}

.content {
    width: 85%;
}
